.top-panel {
  box-sizing: border-box;
}

.top-panel * {
  box-sizing: inherit;
}

.top-panel *:before, .top-panel *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: DinPro;
  src: url(../fonts/DINPro.otf);
}

.top-panel-wrapper {
  position: relative;
  height: 34px;
  margin: 0;
  padding: 0;
}

.top-panel {
  display: flex;
  justify-content: space-between;
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  height: 34px;
  line-height: 34px;
  padding: 0 13px;
  background-color: #000;
  color: #fbea5e;
  font-size: 23.8px;
  text-shadow: 1px 1px 1px rgba(251, 234, 94, 0.6);
  text-transform: uppercase;
  white-space: nowrap;
}

.top-panel .left, .top-panel .right {
  flex: 0;
}

.top-panel .right {
  text-align: right;
}

.top-panel .center {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: hidden;
}

.top-panel .center .jackpot-item {
  display: flex;
  padding: 0 8px;
}

.top-panel .center .jackpot-item .jackpot-name {
  margin-right: 11px;
}

.top-panel .center .jackpot-item .jackpot-counter {
  font-size: 19px;
  position: relative;
}

.top-panel .center .jackpot-item .jackpot-counter .jackpot-counter-wrapper {
  display: flex;
  overflow: hidden;
  background-image: linear-gradient(top, rgba(200, 200, 200, 0.2) 0%, rgba(200, 200, 200, 0.2) 40%, black 45%, black 55%, black 100%);
  background-color: #222222;
  border-radius: 3px;
}

.top-panel .center .jackpot-item .jackpot-counter .jackpot-counter-wrapper .jackpot-counter-cell {
  width: 22.1px;
  height: 34px;
  position: relative;
  transform: rotateX(-12deg);
  transform-style: preserve-3d;
}

.top-panel .center .jackpot-item .jackpot-counter .jackpot-counter-wrapper .jackpot-counter-cell + .top-panel .center .jackpot-item .jackpot-counter .jackpot-counter-wrapper .jackpot-counter-cell {
  box-shadow: inset 0.09em 0 0.1em 0px rgba(255, 255, 255, 0.2);
}

.top-panel .center .jackpot-item .jackpot-counter .jackpot-counter-wrapper .jackpot-counter-cell > div {
  margin: 0 auto;
  height: 34px;
  width: 20px;
  transform-style: preserve-3d;
  will-change: transform;
  transition-property: transform;
  transition-duration: 0ms;
  transition-timing-function: cubic-bezier(0.7, 0.16, 0.3, 0.84);
  transition-delay: 500ms;
}

.top-panel .center .jackpot-item .jackpot-counter .jackpot-counter-wrapper .jackpot-counter-cell .plane {
  position: absolute;
  height: 34px;
  width: 20px;
  text-align: center;
  line-height: 1;
  color: #fbea5e;
  backface-visibility: hidden;
}

.jackpot-counter .figure0 {
  transform: rotate3d(1, 0, 0, 0deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure1 {
  transform: rotate3d(1, 0, 0, -36deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure2 {
  transform: rotate3d(1, 0, 0, -72deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure3 {
  transform: rotate3d(1, 0, 0, -108deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure4 {
  transform: rotate3d(1, 0, 0, -144deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure5 {
  transform: rotate3d(1, 0, 0, -180deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure6 {
  transform: rotate3d(1, 0, 0, -216deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure7 {
  transform: rotate3d(1, 0, 0, -252deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure8 {
  transform: rotate3d(1, 0, 0, -288deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure9 {
  transform: rotate3d(1, 0, 0, -324deg) translate3d(0, 0, 31px);
}
